import React, { useState, useRef, useEffect } from 'react';
import { Link } from 'react-router-dom';
import manifest from './data/navbar.json';

const RenderButton = manifest['Button'].map( (manifest, index) => {

    return (
        <Link to={manifest.Link} key={index}><div className='font-medium text-sm px-5 py-3 text-center text-white rounded-lg bg-blue-600 hover:bg-blue-800'>{manifest.Name}</div></Link>
    )
}
)
const RenderModal = manifest['Modal'].map( (manifest, index) => {

        return (
            <Link to={manifest.Link} key={index}><div className='font-light text-sm pr-10 px-2.5 py-3 text-left text-gray-600 rounded-lg hover:bg-gray-200 hover:text-gray-800'>{manifest.Option}</div></Link>
        )
    }
)

const Modal = () => {
    return (
        <section className='absolute right-6 top-14'>
            <div className='flex flex-col p-2 space-y-2 bg-white rounded-lg shadow-xl border border-gray-300'>
                {RenderModal}
                {RenderButton}
            </div>
        </section>
    )
}

const RenderList = manifest['List'].map( (manifest, index) => {

    return (
        <Link to={manifest.Link} key={index}><div className='font-normal text-base px-5 py-3'>{manifest.Option}</div></Link>
    )
}
)

const List = () => {
return (
    <section className='flex flex-col bg-white w-2/3 md:w-1/5 top-16 left-0 h-screen shadow-xl absolute'>
        {RenderList}
    </section>
    )
}

const Render = () => {

    const [listOpen, setListOpen] = React.useState(false);
    const [modalOpen, setModalOpen] = React.useState(false);

    const handleClick = e => {
        if (node.current.contains(e.target)) {
          // inside click
          console.log("click inside")
          return;
        }
        setListOpen(false); 
        setModalOpen(false);
        console.log("click outside") 
      };

    useEffect(() => {
        // add when mounted
        document.addEventListener("mousedown", handleClick);
        // return function to be called when unmounted
        return () => {
          document.removeEventListener("mousedown", handleClick);
        };
      }, []);

    const node = useRef();
    return (
        <nav ref={node} className='flex flex-row items-center bg-white fixed top-0 w-full z-50 h-16 md:h-20 px-6 border border-gray-300'>
            <svg onClick={()=>{setListOpen(!listOpen); setModalOpen(false);}} className='cursor-pointer w-10 mr-auto p-2' fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16"/></svg>
            <Link to="/" className='absolute left-1/2 -ml-3.5'><img src={require('./images/scclogo.png').default} alt='scc logo' className='w-7' onClick={()=>{setListOpen(false); setModalOpen(false);}} /></Link>
            <button onClick={()=>{setListOpen(false); setModalOpen(!modalOpen);}}  className='ml-auto font-medium text-sm border border-gray-300 hover:border-gray-400 rounded-lg px-2.5 py-1.5 md:hidden'>Sign in</button>
            <div className='flex text-gray-600 hidden md:block'>
                <Link to="/students"><button className='p-2 mx-4 px-3.5 hover:text-black hover:bg-gray-200 rounded-lg'>Student</button></Link>
                <Link to="/staff"><button className='p-2 mx-4 px-3.5 hover:text-black hover:bg-gray-200 rounded-lg'>Teachers</button></Link>
                <Link to="/apply"><button className='p-2 mx-4 px-3.5 text-white font-bold bg-blue-600 hover:bg-blue-800 rounded-lg'>Apply Now</button></Link>
            </div>

            <div onClick={()=>{setListOpen(false); setModalOpen(false);}}>{modalOpen?<Modal />:null}</div>
            <div onClick={()=>{setListOpen(false); setModalOpen(false);}}>{listOpen?<List />:null}</div>
        </nav>
    )
    
}

export default Render
